import React, { useState } from 'react';
import '../../assets/css/loginAdministrador.css';
import Logo from '../../assets/imagen/logito.png';

const LoginAdministrador = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    const handleSubmit = (event: React.FormEvent) => {
        event.preventDefault(); // Prevenir el envío del formulario de la manera tradicional

        // Validación simple con fines de demostración
        if (username === 'admin' && password === '123') {
            alert('Inicio de sesión exitoso');
            window.location.href = '/administrador';
        } else {
            setErrorMessage('Nombre de usuario o contraseña incorrectos');
        }
    };

    return (
        <div>
            <div className="login-container">
                <img src={Logo} alt="logo" className="logito" />
                <h2>Administración</h2>
                <form id="login-form" onSubmit={handleSubmit}>
                    <div className="input-group">
                        <label htmlFor="username">Nombre de usuario</label>
                        <input
                            type="text"
                            id="username"
                            name="username"
                            required
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                        />
                    </div>
                    <div className="input-group">
                        <label htmlFor="password">Contraseña</label>
                        <input
                            type="password"
                            id="password"
                            name="password"
                            required
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />
                    </div>
                    <button type="submit">Iniciar Sesión</button>
                </form>
                <div className="btn-container">
                    <button onClick={() => { window.location.href = '/'; }}>Iniciar sesión como Usuario</button>
                </div>
                {errorMessage && <div className="error-message">{errorMessage}</div>}
            </div>
        </div>
    );
};

export default LoginAdministrador;
